.board {
  --piece-side: 60px;
  display: grid;
  grid-template-columns: repeat(8, var(--piece-side));
}

.chess-piece {
  height: var(--piece-side);
  font-size: 48px;
  text-align: center;
  cursor: pointer;
}

.chess-piece--active {
  background: red;
}



